@import url(https://fonts.googleapis.com/css?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css?family=News+Cycle&display=swap);
/* VARIABLES DECLARATION */
:root {
  --primary: #f4a950;
  --secondary: #ea2348bf;
  --dark: #1d1b1b;
  --darker: #111111;
  --light: #e9e9e9;
  --lighter: #f7f7f7;
  --lightTransparence: #191818d9;
  --blue: #3396b5;
  --bluer: #607d8b;
  --animate-duration: 2500ms;
  --animate-delay: 0.9s;
}
* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
  transition: all 0.8s ease-in-out;
}
html {
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  width: 100%;
}
ul {
  list-style: none;
}
.clear {
  clear: both;
}
.grid {
  display: grid;
  grid-gap: 1rem;
}
.theme-btn {
  width: 38px;
  height: 38px;
  position: fixed;
  bottom: 2rem;
  right: 1rem;
  z-index: 1;
}
/* -------------------------------------- */
/* special classes */
.light {
  background-color: #e9e9e9;
  background-color: var(--light);
  color: #1d1b1b;
  color: var(--dark);
}
.lighter {
  background-color: #f7f7f7;
  background-color: var(--lighter);
  color: #1d1b1b;
  color: var(--dark);
}
.dark {
  background-color: #1d1b1b;
  background-color: var(--dark);
  color: #e9e9e9;
  color: var(--light);
}
.darker {
  background-color: #111111;
  background-color: var(--darker);
  color: #e9e9e9;
  color: var(--light);
}
.to-left {
  float: left;
}
.to-right {
  float: right;
}
/* Loading Styling */
.loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  padding: 1rem;
  margin: 0 auto;
}
.loading h2 {
  margin-bottom: 2rem;
}
.loader {
  border: 5px solid #f3f3f3;
  border-radius: 50%;
  border-top: 5px solid #1d1b1b;
  border-top: 5px solid var(--dark);
  width: 70px;
  height: 70px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 3s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
/* Error styling */
#error {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
#error h1 {
  font-size: 2rem;
  word-spacing: 15px;
  letter-spacing: 3px;
  margin-bottom: 2rem;
}
.error {
  padding: 2rem;
  font-size: 1.3rem;
  border-radius: 15px;
  background: #e28c9cbf;
}
/* media queries */
@media screen and (min-width: 1368px) {
  #root {
    width: 1350px;
    margin: 0 auto;
  }
  #root .topnav {
    width: 1350px;
  }
}
#social-media{
  background: wheat;
  height: 10vh;
  padding: 10%;
}
#social-media p {
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 30px;
}
.social-icons img  {
  width: 60px;
  transition: 0.5s;
}

.social-icons a:hover img {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}
#footer {
  /* background-image: linear-gradient(to right, #051a37, #051a37); */
  background-color: #051a37;
  color:white
}

.footer-img {
  width: 100%;
}

.footer-box {
  padding: 20px;
}
.footer-box img  {
  width: 120px;
  margin-bottom: 20px;
}
.footer-box .fa {
  margin-right: 8px;
  font-size: 25px;
  height: 40px;
  width: 40px;
  text-align: center;
  padding: 7px;
  border-radius: 2px;
  
}
.footer-box .form-control {
  box-shadow: none !important;
  background-color: rgba(212, 212, 212,0.2);
  border:none;
  color: white;
  border-radius: 0;
  margin-top: 25px;
  max-width: 250px;
}
.footer-box .btn-primary {
  box-shadow: none !important;
  border:none;
  border-radius: 0;
  margin-top: 30px;


}
hr {
  background-color: white;
}
.copyright {
  margin-bottom: 0;
  padding-bottom: 20px;
  text-align: center;
}
/* Custom Style */
.nav-social {
  width: 100%;
  height: 36px;
  padding: 0 2rem;
  background: var(--lightTransparence);
  color: var(--primary);
}
.nav-social ul li {
  display: inline-block;
  padding: 0.5rem 0.7rem;
  border-right: 1px solid #fff;
}
.nav-social ul.to-right li:hover {
  opacity: 0.5;
}
.nav-social ul li:nth-child(1) {
  border-left: 1px solid #fff;
}
/* Nav Bar Items Style */
.sticky {
  background: var(--lightTransparence);
  top: 0;
}
.topnav {
  width: 100%;
  height: 42px;
  color: #fff;
  text-transform: uppercase;
  padding: 0 2rem;
  position: fixed;
  z-index: 2;
  overflow: hidden;
}
.topnav .menu {
  text-transform: capitalize;
}
.menu .nav-item {
  display: inline-block;
  cursor: pointer;
  background: none;
  text-align: center;
  text-decoration: none;
  font-weight: 500;
  font-family: "News Cycle", sans-serif;
  padding: 0.5rem 0.7rem;
}
.nav-item:hover {
  text-transform: uppercase;
}
.nav-item:focus {
  outline: none;
}
.topnav .logo {
  cursor: default;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0.6rem;
}
.topnav .navBtn {
  display: none;
}
.topnav .navBtn:hover {
  background: none;
  color: var(--blue);
}
.topnav .active {
  background: var(--lightTransparence);
  color: var(--blue);
}

/* Screen styles */
@media screen and (max-width: 728px) {
  .nav-social {
    text-align: center;
  }
  .nav-social .to-left {
    display: none;
  }
  .nav-social .to-right,
  .nav-social .to-left {
    float: none;
  }
}
@media screen and (max-width: 725px) {
  .topnav .navBtn {
    cursor: pointer;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    padding: 0.9rem;
  }
}
@media screen and (max-width: 620px) {
  .topnav {
    padding: 0;
  }
  .topnav .logo {
    text-align: center;
    padding: 0.7rem;
    float: none;
  }
  .topnav .menu .nav-item:hover {
    border: none;
  }
  .responsive .menu .nav-item {
    float: none;
    display: block;
    text-align: left;
  }
  .responsive .menu .nav-item a {
    width: 100%;
    display: block;
  }
  .responsive {
    height: auto;
    z-index: 2;
  }
  .responsive .menu {
    float: none;
  }
  .responsive .active {
    border: none;
  }
}

header {
  background: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  background: url('https://images.unsplash.com/photo-1533750516457-a7f992034fec?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1381&q=80');
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
#landing-header {
  width: 100%;
  background: #000000a6;
  height: calc(100vh - 36px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 1;
}
#landing-header .header {
  margin-left: 15vw;
}
.header h1 {
  font-size: 3rem;
  font-family: 'Abel', sans-serif;
  color: var(--primary);
  margin: 0.7rem 0;
}
.header p {
  width: 65%;
  color: #fff;
  margin: 0.7rem 0;
}
.header .btn {
  font-family: 'Abel', sans-serif;
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: capitalize;
  display: inline-block;
  padding: 0.3rem 0.5rem;
  margin-top: 0.5rem;
  margin-right: 1rem;
  border: none;
  border-radius: 3px;
}
.btn.btn-primary {
  color: var(--dark);
  background: var(--primary);
  border: 1px solid var(--primary);
}
.btn.btn-secondary {
  color: var(--dark);
  background: var(--blue);
  border: 1px solid var(--blue);
}
.btn.btn-primary:hover {
  background: none;
  border-radius: 3px;
  color: var(--primary);
  border: 1px solid var(--primary);
}
.btn.btn-secondary:hover {
  background: none;
  border-radius: 3px;
  color: var(--blue);
  border: 1px solid var(--blue);
}
/* Media Queries */
@media screen and (max-width: 728px) {
  #landing-header .header {
    text-align: center;
    margin: 0 auto;
  }
  .header p {
    display: none;
  }
}
@media screen and (max-width: 520px) {
  .header h1 {
    font-size: 2rem;
  }
}
.typing-animation {
  overflow: hidden; /* Hide overflowing content */
  white-space: nowrap; /* Prevent line breaks */
  -webkit-animation: typing 5s steps(40, end) forwards;
          animation: typing 5s steps(40, end) forwards;
}

@-webkit-keyframes typing {
  from {
    width: 0; /* Start with no width */
  }
  to {
    width: 100%; /* End with full width */
  }
}

@keyframes typing {
  from {
    width: 0; /* Start with no width */
  }
  to {
    width: 100%; /* End with full width */
  }
}





footer .details {
  padding: 4rem 2rem;
  margin-bottom: 1rem;
}
footer .copyrights {
  width: 100%;
  text-align: center;
  background: var(--lightTransparence);
  color: var(--light);
  padding: 1rem;
  position: relative; 
}
footer span {
  text-transform: uppercase;
}
footer span span {
  color: var(--primary);
}
/* subscribe style */
.subscribe {
  padding: 1rem;
  background: var(--lightTransparence);
  color: var(--light);
}
.subscribe form {
  display: inline-block;
}
.subscribe form label {
  font-family: "Abel", sans-serif;
  font-weight: bold;
}
.subscribe form input[type="email"] {
  background: var(--light);
  border: 1px solid var(--dark);
  border-radius: 3px;
  padding: 0.5rem;
  margin: 0 0.25rem;
}
.subscribe form input[type="submit"] {
  background: var(--dark);
  color: var(--light);
  border: none;
  border-radius: 3px;
  padding: 0.5rem;
}
.subscribe ul {
  padding: 0.5rem 0;
}
.subscribe ul li {
  display: inline-block;
  color: var(--blue);
  margin: 0 0.25rem;
}
.subscribe ul li:hover {
  color: var(--primary);
}
/* Details Style */
.footer-infos.grid {
  text-align: center;
  grid-template-columns: repeat(4, 1fr);
}
.footer-infos h3 {
  font-size: 1rem;
  margin-bottom: 1.5rem;
}
.footer-infos li {
  cursor: pointer;
  font-family: "Abel", sans-serif;
  font-weight: bold;
  font-size: 0.9rem;
  margin-bottom: 0.4rem;
}
.footer-infos li:hover {
  color: var(--blue);
}
.footer-infos ul.icons {
  margin-top: 1rem;
}
.footer-infos li.icon-item {
  display: inline-block;
  color: var(--primary);
}
/*  to top button */
.to-top {
  z-index: 2;
  right: 1rem;
  width: 38px;
  height: 38px;
  border: none;
  bottom: 1rem;
  display: flex;
  cursor: pointer;
  color: var(--dark);
  position: fixed;
  border-radius: 50%;
  justify-items: center;
  flex-direction: column;
  justify-content: center;
  background: var(--primary);
}
/* Media Queries */
@media screen and (max-width: 768px) {
  footer .footer-infos.grid {
    text-align: center;
  }
  .footer-infos.grid {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 500px) {
  .subscribe form {
    width: 100%;
    text-align: center;
  }
  .subscribe ul {
    display: none;
  }
  .footer-infos.grid {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .footer-infos.grid div:not(:nth-child(4)) {
    display: none;
  }
}
#copy{
  margin-top: 15px;
  color: whitesmoke;
}

main h1,
main h2,
main h3,
main h5 {
  margin: 0.7rem 0;
}
main span {
  color: var(--primary);
}
main p {
  line-height: 1.5rem;
  margin-bottom: 0.7rem;
}
.main-content .main-title {
  text-align: center;
  margin: 4rem 0;
}
.main-title h5 {
  text-transform: uppercase;
}
.button {
  font-size: 1rem;
  font-family: 'Abel', sans-serif;
  font-weight: bold;
  text-transform: capitalize;
  border: none;
  padding: 0.4rem 0.7rem;
  border-radius: 25px;
  background: var(--primary);
}
/* services style */
.services.grid {
  margin: 0 2rem;
  grid-template-columns: repeat(3, 1fr);
}
.work.grid,
.about.grid {
  grid-template-columns: 1fr 1fr;
  margin: 4rem 0;
}
.work.grid {
  padding: 4rem 2rem;
}
.about.grid {
  margin: 0 2rem;
  background: var(--light);
}
/* Media Queries */
@media screen and (max-width: 728px) {
  .services.grid {
    margin: 2rem 0;
    grid-template-columns: 1fr 1fr;
    padding: 0 1rem;
  }
}
@media screen and (max-width: 520px) {
  .services.grid {
    margin: 1.25rem 0;
    grid-template-columns: 1fr;
  }
  .work.grid,
  .about.grid,
  .about-us.grid {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .about.grid img {
    height: auto;
  }
  .about.grid p {
    text-align: justify;
    width: 76%;
    margin: 0.7rem auto;
  }
}

.about img {
  height: 100%;
}
.about .about-div {
  padding: 1rem 0.5rem;
}
.about-section {
  background: var(--light);
  padding: 4rem 2rem;
  margin: 4rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
  text-align: center;
}
.about-title {
  margin-bottom: 2rem;
}
.about-title h1 {
  font-size: 2rem;
}
.about-title p {
  font-size: 1rem;
  text-transform: uppercase;
  color: var(--blue);
}
.about-us {
  grid-template-columns: 1fr 1fr;
}
.about-us div {
  padding: 1rem 1.5rem;
  background: #fff;
}
.about-us div img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 0.5rem;
}
.about-us div p {
  margin-top: 1rem;
  line-height: 1.9rem;
}

.features {
  padding: 0 2rem;
  grid-template-columns: repeat(4, 1fr);
  margin-bottom: 4rem;
}
.features div {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  align-content: center;
}
.features div i {
  color: var(--primary);
}
.features div p {
  font-size: 0.9rem;
}
@media screen and (max-width: 728px) {
  .features {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 520px) {
  .features {
    grid-template-columns: 1fr;
  }
}

.service {
  width: 100%;
  background: var(--light);
}
.service img {
  height: 275px;
}
.service .service-info {
  padding: 1rem;
}
.service-info h4 {
  margin-bottom: 0.7rem;
}

@media screen and (max-width: 1020px) {
  .service img {
    height: 200px;
  }
}
@media screen and (max-width: 920px) {
  .service img {
    height: 175px;
  }
}
@media screen and (max-width: 720px) {
  .service img {
    height: 170px;
  }
}
@media screen and (max-width: 520px) {
  .service img {
    height: auto;
  }
}

.work.grid {
  background: var(--lightTransparence);
  color: #fff;
}
.work ul.grid {
  grid-template-columns: 1fr 1fr;
}
.work ul {
  padding: 1rem 0;
}
.work ul li {
  padding: 1rem;
  background: var(--dark);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.work ul li span {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--blue);
}
.work ul li p {
  margin: 0.3rem 0;
}

