.about img {
  height: 100%;
}
.about .about-div {
  padding: 1rem 0.5rem;
}
.about-section {
  background: var(--light);
  padding: 4rem 2rem;
  margin: 4rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
  text-align: center;
}
.about-title {
  margin-bottom: 2rem;
}
.about-title h1 {
  font-size: 2rem;
}
.about-title p {
  font-size: 1rem;
  text-transform: uppercase;
  color: var(--blue);
}
.about-us {
  grid-template-columns: 1fr 1fr;
}
.about-us div {
  padding: 1rem 1.5rem;
  background: #fff;
}
.about-us div img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 0.5rem;
}
.about-us div p {
  margin-top: 1rem;
  line-height: 1.9rem;
}
